	},
	watch: {
		title1() {
			// console.log(this.title1)
			this.chapterList(this.title1)
			this.title2 = "暂无数据"
			this.title3 = "暂无数据"
			this.chapterLists = [],
				this.lessonLists = [],
				this.list = []
		},
		title2() {
			this.lessonList(this.title1, this.title2)
		}
	},
	methods: {
		actives1() {
			if (this.show == true && (this.show2 == true || this.show3 == true)) {
				this.show = this.show
				// console.log(1)
			} else if (this.show == true && this.show1 == false) {
				this.show = false
				// console.log(2)
			} else {
				this.show = !this.show
				// console.log(3)
			}
			// console.log(this.show)
			this.active1 = !this.active1
			this.show1 = !this.show1
			this.show2 = false
			this.show3 = false
			this.active2 = false
			this.active3 = false
			// console.log(this.active1, 'active1')
		},
		actives2() {
			if (this.show == true && (this.show1 == true || this.show3 == true)) {
				this.show = this.show
			} else {
				this.show = !this.show
			}

			this.active2 = !this.active2
			this.show2 = !this.show2
			this.show1 = false
			this.show3 = false
			this.active1 = false
			this.active3 = false
			// console.log(this.active2, 'active2')
		},
		actives3() {
			if (this.show == true && (this.show1 == true || this.show2 == true)) {
				this.show = this.show
			} else {
				this.show = !this.show
			}

			this.show1 = false
			this.show2 = false
			this.active2 = false
			this.active1 = false
			this.active3 = !this.active3
			this.show3 = !this.show3
			// console.log(this.active3)
		},
		optionsActive1(val) {
			this.title1 = val
			// console.log(this.title1, this.thesaurusId1)
			this.show = !this.show
			this.show1 = false
			this.active1 = !this.active1
		},
		optionsActive2(val) {
			this.title2 = val
			// console.log(val)
			this.show = !this.show
			this.show2 = false
			this.active2 = !this.active2
		},
		optionsActive3(val) {
			this.title3 = val
			// console.log(val)
			this.show = !this.show
			this.show3 = false
			this.active3 = !this.active3


			// console.log(999)
			// console.log(this.title1)
			// console.log(this.title2)
			// console.log(this.title3)
			this.wordListByChapter()

		},
		// 全选
		onChange(event) {
			if (event.detail == false) {
				this.result = []
			}
			if (event.detail == true) {
				this.result = []
				this.list.forEach(data => {
					// console.log(data)						
					this.result.push(data.wordId.toString())
				})

				// 保存到storage里面
				uni.setStorage({
					key: 'selectedWords',
					data: this.list
				});

			}
			console.log(this.result)
			this.wordCount = this.result.length
			this.checkAll = event.detail
			// this.lookSelects = event.detail
		},

		onChanges(event) {
			this.result = event.detail
			// console.log(this.result)

			this.result.forEach(data => {
				this.list.forEach(val => {
					if (data == val.wordId) {
						this.arr.push(val)
					}
				})
			})
			// 去除重复
			let hash = {}
			this.arr = this.arr.reduce((preVal, curVal) => {
				hash[curVal.wordId] ? '' : hash[curVal.wordId] = true && preVal.push(curVal);
				return preVal
			}, [])

			// console.log(this.arr)

			// 取消选择的单词
			if (this.result.length == 0) {
				this.arr = []
			} else {
				this.arrb = []
				this.result.forEach(data => {
					this.arr.forEach(val => {
						if (data == val.wordId) {
							console.log(val)
							this.arrb.push(val)
						}
					})
				})
			}
			// 去重复
			let hashb = {}
			this.arrb = this.arrb.reduce((preVal, curVal) => {
				hashb[curVal.wordId] ? '' : hashb[curVal.wordId] = true && preVal.push(curVal);
				return preVal
			}, [])
			console.log(this.arrb)
			// 传值给父级
			this.$emit('sendCwords', this.arrb)
		},
		
		// 获取课程下拉列表
		thesaurusList() {
			this.$minApi.thesaurusList({}).then(data => {
				// console.log(data)
				this.thesaurusLists = data.data
				this.title1 = data.data[0].thesaurusName
			})
			console.log(this.title1)
			// this.chapterList(this.title1)
		},
		// 获取章下拉列表
		chapterList(thesauruName) {
			this.$minApi.chapterList({
				thesauruName: thesauruName
			}).then(data => {
				console.log(data)
				this.chapterLists = data.data
				if (data.data.length != 0) {
					this.title2 = data.data[0].belong_chapter
					// this.lessonList(this.title1, this.title2)
				}
			})
		},
		// 获取节下拉列表
		lessonList(thesauruName, chapter) {
			// console.log(chapter, thesauruName)
			this.$minApi.lessonList({
				chapter: chapter,
				thesauruName: thesauruName
			}).then(data => {
				console.log(data)
				this.lessonLists = data.data
				if (data.data.length != 0) {
					this.title3 = data.data[0].belong_lesson
					// console.log(data.data[0].belong_lesson)
					this.wordListByChapter()
				}
			})
		},
		// 通过课程章节获取单词列表
		wordListByChapter() {
			// console.log(this.title3,lesson)
			this.$minApi.wordListByChapter({
				thesauruName: this.title1,
				chapter: this.title2,
				lesson: this.title3
			}).then(data => {
				this.list = data.data
				console.log(data)
			})
		},
	
